@include phone-landscape{
	.qode-int-icon-showcase{
		.qode-int-icon-circle,
		.qode-int-icon-showcase-inner{
			width: calc(100% - 60px);
			height: calc(100% - 60px);
			margin: 30px;
		}
	}

	.qode-showcase-icon{
		.qode-icon-holder{
			width: 55px;
			height: 55px;
			line-height: 55px;
			font-size: 20px;
		}
	}
	.qode-showcase-content-inner{
		display: none;
	}
}