@include laptop-big-landscape {
    .qode-workflow{
        .qode-workflow-item{
            max-width: 90%;

        }
    }
}
@include laptop-landscape {
    .qode-workflow{
        .qode-workflow-item{
            max-width: 100%;

            .qode-workflow-item-inner .qode-workflow-text{
                padding: 0px 40px;
            }
        }
    }
}

@include phone-landscape{

    .qode-workflow {

        .main-line,
        .qode-workflow-item .line,
        .qode-workflow-item .qode-workflow-text .circle{
            display: none !important;
        }

        .qode-workflow-item {

            .qode-workflow-item-inner.reverse {
                display: block;
                -webkit-box-direction: normal;
                -webkit-flex-direction: row;
                -ms-flex-direction: row;
                flex-direction: row;
                -webkit-flex-wrap: nowrap;
                -ms-flex-wrap: nowrap;
                flex-wrap: nowrap;

	            .qode-workflow-text {
		            text-align: left;
	            }
            }
        }

        .qode-workflow-text{
            width: 100% !important;
            padding: 0px !important;
        }

        .qode-workflow-image{
            width: 100% !important;
            margin-bottom: 20px !important;
        }
    }
}