/* ==========================================================================
Expanding Images shortcode responsive styles - start
========================================================================== */
@include phone-portrait {
    .qode-expanding-images {
        .qode-expanding-images-inner {
            max-width: 80%;
        }

        .qode-hero-image {
            .qode-hero-image-link {
                &:hover {
                    img{
                        @include qodeTransform(translate3d(0,-10px,0));   
                    }
                }
            }
        }
        
        .qode-side-image {
            &.qode-side-image-inner {
                width: 25%;
                padding-top: 15.6255%;
            }
            &.qode-side-image-1 {
                top: 13%;
                left: 4.5%;
            }
            &.qode-side-image-2 {
                bottom: 21%;
                left: 3.3%;
            }
            &.qode-side-image-3 {
                top: 13%;
                right: 4.5%;
            }
            &.qode-side-image-4 {
                bottom: 21%;
                right: 3.3%;
            }
            &.qode-side-image-outer {
                display: none;
            }
        }
    }
}
/* ==========================================================================
Expanding Images shortcode responsive styles - end
========================================================================== */