/* ==========================================================================
   #Comparative Features Table style - start
   ========================================================================== */

@include ipad-portrait {
	.qode-comparative-features-table{
		.qode-cft-feature-item-title-responsive {
			display: inline-block;
		}

		.qode-cft-title-holder,
		.qode-cft-feature-title,
		.qode-cft-link-holder
		{
			display: none;
		}

		.qode-cft-mark{
			display: block;
		}
	}
}

@include phone-landscape {

	.qode-comparative-features-table{
		.qode-cft-feature-item-title-responsive {
			font-size: 14px;
			padding: 0 5px;
		}

		.qode-cft-column-footer-image{
			padding: 8px;
			width: 30%;
		}

		.qode-cft-column-footer-text{
			padding-right: 8px;
		}

		.qode-cft-column-link-holder{
			padding: 15px 8px;
		}

	}
}

/* ==========================================================================
   #Comparative Features Table style - end
   ========================================================================== */