@include ipad-landscape {
    .cards,
    .cards .card{
        height: 40px;
    }

    .qode-card-slider-holder-outer {
        padding-top: 30px;
        padding-bottom: 60px;

        .controls{
            &.bullets {
                bottom: -43px;
            }

            .button{
                &.prev{
                    left: 30px;
                }
                &.next{
                    right: 30px;
                }
            }
        }
    }
}

@include phone-landscape{
    .qode-card-slider-holder-outer {
        .controls.arrows {
            .button {
                display: none;
            }
        }
    }
}

@include phone-portrait {
    .cards,
    .cards .card{
        height: 15px;
    }

    .qode-card-slider-holder-outer {
        padding-top: 20px;
        padding-bottom: 35px;

        .controls.arrows {
            .button {
                display: none;
            }
        }

        .controls.bullets{
            bottom: -30px;

            .dots .dots-inner .dot{
                margin: 0px 3px;
            }
        }
    }


}
