/* ==========================================================================
   Multi-Device Showcase start responsive styles
   ========================================================================== */
#qode-multi-device-showcase {
	@include ipad-landscape {
		.qode-mds-content-holder {
			width: 50%;
		}

		.qode-mds-device-holder {
			&.qode-mds-laptop-slider-holder {
			    top: 16%;
			    left: 62%;
				width: 70%;
			}

			&.qode-mds-tablet-slider-holder {
				top: -25%;
				left: 36%;
				width: 35%;
			}

			&.qode-mds-phone-slider-holder {
				top: 59%;
				left: 48%;
				width: 19%;
			}
		}

		.qode-mds-additional-element {    
		    display: none; //leave sliders only
		}
	}

	@include ipad-portrait {
		.qode-mds-content-holder {
			width: 70%;
		}

		.qode-mds-device-holder {
			&.qode-mds-laptop-slider-holder {
				top: 38%;
				left: 56%;
				width: 100%;
			}

			&.qode-mds-tablet-slider-holder {
				top: -10%;
				left: 39%;
				width: 44%;
			}

			&.qode-mds-phone-slider-holder {
				top: 74%;
			    left: 34%;
				width: 25%;
			}
		}
	}

	@media only screen and (max-width: 736px) {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		min-height: 100vh;
	    height: auto !important; //override fullscreen js

		.qode-mds-content-holder {
			display: block;
		    width: 100%;
		    box-sizing: border-box;
			padding: 12%; 
		}

		.qode-mds-device-holder {
			&.qode-mds-laptop-slider-holder {
				@include qodeTransform(none);
				position: static;
				max-width: 80%;
				margin: auto;
			}
		}

		.qode-mds-tablet-slider-holder,
		.qode-mds-phone-slider-holder {
			display: none; //leave laptop slider only
		}
	}
}
/* ==========================================================================
   Multi-Device Showcase end responsive styles
   ========================================================================== */