/* ==========================================================================
   Elliptical Slider shortcode responsive start styles
   ========================================================================== */
@include ipad-landscape{
	.qode-elliptical-slider{
		.flex-control-nav.flex-control-paging {
			left: 20px;
		}
	}
	.qode-elliptical-slider .qode-elliptical-slide .qode-elliptical-slide-image-holder-wrapper {
		.qode-elliptical-slide-image-holder {
			display: none;
		}
	}

}
@include ipad-portrait {

	.qode-elliptical-slider {

		.qode-elliptical-slide {

			height: 450px;

			.qode-elliptical-slide-image-holder-wrapper {

				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
				background-color: rgba(41, 41, 41, 1);



			}

			.qode-elliptical-slide-content-wrapper {

				.qode-elliptical-slide-wrapper-inner {
					width: 100%;
				}

				.qode-elliptical-slide-wrapper-inner svg {
					display: none;
				}

				.qode-elliptical-slide-elements-holder {
					padding: 0;
				}
			}

			.qode-elliptical-slide-content-holder {
				background: none !important;
			}

		}
	}
}

@include ipad-landscape{
  .qode-elliptical-slide {

	height: 600px;

  }
}

@include smaller-phone-portrait {

  .qode-elliptical-slide {
	.qode-elliptical-slide-content-wrapper {
	  .qode-elliptical-slide-elements-holder {
		padding: 0 35px;
	  }
	}
  }
}

/* ==========================================================================
   Fullwidth Slider shortcode responsive end styles
   ========================================================================== */