/* ==========================================================================
Call To Action Section shortcode responsive styles - start
========================================================================== */
@media only screen and (max-width: 1280px) {
	.qode-cta-section {
		.qode-cta-section-text-wrapper {
			.qode-cta-section-title {
				font-size: 2.5em;
			}

			.qode-cta-section-description-holder {
				margin-bottom: 35px;
			}

			.qode-cta-section-description {
				font-size: 1em;
			}
		}
	}
}

@include ipad-landscape {
	.qode-cta-section {
		img {
			@include qodeTransform(scale(1.25));
		}

		.qode-cta-section-text-wrapper {
			width: 520px;
			height: 520px;

			.qode-cta-section-title {
				font-size: 2.2em;
			}

			.qode-cta-section-description {
				font-size: 1.2em;
			}
		}
	}
}

@include ipad-portrait {
	.qode-cta-section {
		overflow: hidden;

		img {
			@include qodeTransform(scale(1.75));
		}
	}
}

@include phone-landscape { 
	.qode-cta-section {
		img {
			display: none;
		}

		.qode-cta-section-text-wrapper {
			height: auto;
			width: auto;
			position: relative;
			top:0;
			left:0;
			@include qodeTransform(none);

			.qode-cta-section-title {
				font-size: 2em;
			}

			.qode-cta-section-description-holder {
				margin-bottom: 45px;
			}

			.qode-cta-section-description {
				font-size: 1em;
			}
		}
	}
}

/* ==========================================================================
Call To Action Section shortcode responsive styles - end
========================================================================== */