/* ==========================================================================
   Item Showcase start styles
	========================================================================== */
@media only screen and (max-width: $laptop-landscape) {
	 
	.qode-item-showcase {
		.qode-item-image {
			margin-top: 0 !important;    //override inline margin if set by user
			margin-bottom: 60px;
		}

		.qode-item-showcase-holder.qode-holder-left .qode-item-line {
			right: 38px;
		}

		.qode-item-showcase-holder.qode-holder-right .qode-item-line {
			left: 38px;
		}
	}

}

@media only screen and (max-width: $laptop-landscape) and (min-width: $ipad-landscape) {
	.qode-item-showcase {
		  	.qode-item-showcase-holder.qode-holder-left,
		 	.qode-item-showcase-holder.qode-holder-right {
		  	    width:30%;
		  	}
	}
}

@media only screen and (max-width: $ipad-landscape) {
	.qode-item-showcase {
		.qode-item-image {
			width: 100%;
			float: none;
		}

	 	.qode-item-showcase-holder.qode-holder-left,
		.qode-item-showcase-holder.qode-holder-right {
	 	    float: left;    
	 	    width: 50%;
		    @include qodeBoxSizing(border-box);
		    padding:0 15px;
	 	}
	}
 }

 @media only screen and (max-width: $phone-landscape) {
 	.qode-item-showcase {
	 	.qode-item-showcase-holder.qode-holder-left,
		.qode-item-showcase-holder.qode-holder-right {
	 	    float: none;    
	 	    width: 100%;
		    @include qodeBoxSizing(border-box);
		    padding:0 15px;
	 	}
 	}
 }
/* ==========================================================================
  Item Showcase end styles
  ========================================================================== */