/* ==========================================================================
  	Scrolling Image shortcode start responsive styles
   	========================================================================== */
@include laptop-landscape-medium {
	.qode-scrolling-image-holder {
		$scrolling-icon-size: 70px;

		.qode-si-icon-holder {
			height: $scrolling-icon-size;
			width: $scrolling-icon-size;
			bottom: -$scrolling-icon-size/2;

			.qode-si-icon {
				font-size: 40px;
				line-height: 1.1em;
			}
		}
	}
}

@include ipad-landscape {
	.qode-scrolling-image-holder {
		$scrolling-icon-size: 60px;

		.qode-si-icon-holder {
			height: $scrolling-icon-size;
			width: $scrolling-icon-size;
			bottom: -$scrolling-icon-size/2;

			.qode-si-icon {
				font-size: 35px;
				line-height: 1.1em;
			}
		}
	}
}

@include ipad-portrait {
	.qode-scrolling-image-holder {
		$scrolling-icon-size: 48px;

		.qode-si-icon-holder {
			height: $scrolling-icon-size;
			width: $scrolling-icon-size;
			bottom: -$scrolling-icon-size/2;
		}
	}
}
/* ==========================================================================
  	Scrolling Image shortcode end responsive styles
  	========================================================================== */
