/* ==========================================================================
   #Numbered Process style - start
   ========================================================================== */
.qode-numbered-process-holder{

	&.qode-numbered-process-holder-items-three{
		@include ipad-landscape{
			.qode-numbered-process-inner{
				margin: 0 -30px;
			}

			.qode-np-item{
				padding: 0 30px;
			}

			.qode-np-line{
				width: 60px;
			}
		}

		@include ipad-portrait{
			.qode-numbered-process-inner{
				margin: 0 -20px;
			}

			.qode-np-item{
				padding: 0 20px;
			}

			.qode-np-line{
				width: 40px;
			}
		}

		@include phone-landscape{
			.qode-numbered-process-inner{
				margin: -10px 0;
			}

			.qode-np-item{
				width: 100%;
				padding: 10px 0;
			}

			.qode-np-line{
				width: 0;
			}
		}
	}

	&.qode-numbered-process-holder-items-four{
		@include laptop-landscape{
			.qode-numbered-process-inner{
				margin: 0 -20px;
			}

			.qode-np-item{
				padding: 0 20px;
			}

			.qode-np-line{
				width: 40px;
			}
		}

		@include ipad-portrait{
			.qode-numbered-process-inner{
				margin: -10px -30px;
			}

			.qode-np-item{
				width: 50%;
				padding: 10px 30px;

				&:nth-child(2n) .qode-np-line{
					border: none;
				}
			}

			.qode-np-line{
				width: 60px;
			}
		}

		@include phone-landscape{
			.qode-numbered-process-inner{
				margin: -10px 0;
			}

			.qode-np-item{
				width: 100%;
				padding: 10px 0;
			}

			.qode-np-line{
				width: 0;
			}
		}
	}

	&.qode-numbered-process-holder-items-five{
		@media only screen and (max-width: 1400px){
			.qode-numbered-process-inner{
				margin: 0 -20px;
			}

			.qode-np-item{
				padding: 0 20px;
			}

			.qode-np-line{
				width: 40px;
			}
		}

		@include ipad-portrait{

			.qode-numbered-process-inner{
				margin: -10px -30px;
			}

			.qode-np-item{
				width: 50%;
				padding: 10px 30px;

				&:nth-child(2n) .qode-np-line{
					border: none;
				}
			}

			.qode-np-line{
				width: 60px;
			}
		}

		@include phone-landscape{
			.qode-numbered-process-inner{
				margin: -10px 0;
			}

			.qode-np-item{
				width: 100%;
				padding: 10px 0;
			}

			.qode-np-line{
				width: 0;
			}
		}
	}
}
/* ==========================================================================
   #Numbered Process style - end
   ========================================================================== */